import { Box, IconButton, Image } from "@chakra-ui/react"
import { PictureIcon } from "../../../../components/Icons/Icons"

export default function ImageDetail({ id, src, onOpen, setSelectedImageSrc }) {
    const _handleImageDetail = () => {
        setSelectedImageSrc(src)
        onOpen()
    }

    return (
        <Box
            borderWidth="1px"
            rounded="lg"
            shadow="lg"
            position="relative">
            <IconButton
                position="absolute"
                bottom={2}
                right={2}
                bg="whiteAlpha.300"
                icon={<PictureIcon />}
                onClick={_handleImageDetail}
            />
            <Image
                key={id}
                h='200px'
                w={"100%"}
                alt={`Picture`}
                src={src}
                roundedTop="lg" />
        </Box>
    )
}